import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { AppStoreType, type AppDispatch } from "./bll/store";
import { loadingAC } from "./bll/loadingReducer";
import SuperButton from "../hw04/common/c2-SuperButton/SuperButton";
import s2 from "../../s1-main/App.module.css";
import { Loader } from "./Loader";

/*
1 -   loadingReducer.ts    
2 -  isLoading  
3 -   setLoading
4 -      
*/

const HW10 = () => {
	// useSelector, useDispatch //  
	// const isLoading = false;

	const selectlLoading = (state: AppStoreType) => state.loading.isLoading;
	const isLoading = useSelector(selectlLoading);
	const dispatch = useDispatch<AppDispatch>();

	const setLoading = () => {
		//   //    1,5 
		// dispatch
		dispatch(loadingAC(true));

		// setTimeout
		const timeOutId = setTimeout(() => dispatch(loadingAC(false)), 1500);
	};

	return (
		<div id={"hw10"}>
			<div className={s2.hwTitle}>Homework #10</div>

			<div className={s2.hw}>
				{isLoading ? (
					<div id={"hw10-loading"}>
						<Loader />
					</div>
				) : (
					<SuperButton id={"hw10-button-start-loading"} onClick={setLoading}>
						Set loading...
					</SuperButton>
				)}
			</div>
		</div>
	);
};

export default HW10;
